form {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center; }
  form input {
    margin-right: 10px;
    width: 50%; }
    form input::-webkit-input-placeholder {
      font-family: 'Font Awesome 5 Free';
      font-weight: 900; }
    form input::-moz-placeholder {
      font-family: 'Font Awesome 5 Free';
      font-weight: 900; }
    form input:-ms-input-placeholder {
      font-family: 'Font Awesome 5 Free';
      font-weight: 900; }
    form input::-ms-input-placeholder {
      font-family: 'Font Awesome 5 Free';
      font-weight: 900; }
    form input::placeholder {
      font-family: 'Font Awesome 5 Free';
      font-weight: 900; }
  form .search__keyboard {
    border: none;
    cursor: pointer;
    color: var(--swiper-theme-color);
    background-color: white; }
  form .search__speak {
    border: none;
    color: var(--swiper-theme-color);
    background-color: white;
    width: 50px; }
    form .search__speak::after {
      font-family: 'Font Awesome 5 Free';
      font-weight: 900;
      content: '\f130';
      margin-left: 5px; }
    form .search__speak.active::after {
      font-family: 'Font Awesome 5 Free';
      font-weight: 900;
      content: '\f131'; }
  form .search__submit {
    margin-left: 10px; }

.wrapper__virtual-keyboard {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: white;
  z-index: 10;
  display: flex;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box; }
  .wrapper__virtual-keyboard .wrapper__virtual-close {
    position: absolute;
    top: 5px;
    right: 5px;
    border: none; }

@media all and (max-width: 768px) {
  .search__keyboard {
    display: none; }
  .wrapper__virtual-keyboard {
    display: none; } }
