.swiper-container {
  width: 100%; }
  .swiper-container .swiper-wrapper {
    box-sizing: border-box;
    padding: 40px 0; }
  .swiper-container .swiper-slide {
    width: 300px;
    height: 400px; }

.movies-wrapper {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  padding: 40px;
  box-sizing: border-box; }

.swiper-movie {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 2px; }
  .swiper-movie a {
    text-decoration: none;
    color: black;
    height: 50px;
    text-align: center; }
  .swiper-movie a,
  .swiper-movie img,
  .swiper-movie p,
  .swiper-movie div {
    margin: 2px; }
  .swiper-movie img {
    width: 100%;
    height: 300px; }
  .swiper-movie .rating {
    display: flex;
    align-items: center; }
    .swiper-movie .rating .star {
      width: 15px;
      height: 15px;
      margin-right: 5px; }
