.keyboard {
  display: grid;
  grid-template-columns: repeat(15, 40px);
  grid-template-rows: repeat(5, 30px);
  grid-gap: 5px; }

.keyboard__key {
  grid-column: span 1;
  transition: background-color 1s ease-out;
  background-color: white;
  cursor: pointer; }

.keyboard__key:active,
.keyboard__key_active {
  background-color: var(--swiper-theme-color); }

.keyboard__key_two-grid-column {
  grid-column: span 2; }

.keyboard__key_three-grid-column {
  grid-column: span 3; }

.keyboard__key_nine-grid-column {
  grid-column: span 9; }
